import React from "react";

import { Row, Column } from "components/Grid";

import HashLinkDestination from "components/foundational/HashLinkDestination";

import {
  Container,
  ContentWrapper,
  Title,
  Subtitle,
  MarketoForm,
} from "./elements";

type FormProps = { formId: number; munchkinId: string };

type ContentProps = {
  title: string;
  subtitle: string;
  headerOffset: number;
  formProps: FormProps;
};

const Bottom = ({ title, subtitle, formProps, headerOffset }: ContentProps) => (
  <HashLinkDestination id="bottom" headerOffset={headerOffset}>
    <Container>
      <Row padding={30} gutterWidth={26}>
        <Column span={1} spanMedium={0} />
        <Column span={10} spanMedium={12}>
          <ContentWrapper>
            <Title>{title}</Title>
            <Subtitle>{subtitle}</Subtitle>
            <MarketoForm
              formId={formProps.formId}
              munchkinId={formProps.munchkinId}
              successCallback={undefined}
              className={undefined}
              formLoadedCallback={undefined}
            />
          </ContentWrapper>
        </Column>
        <Column span={1} spanMedium={0} />
      </Row>
    </Container>
  </HashLinkDestination>
);

export default Bottom;
