import React from "react";

import { Row, Column } from "components/Grid";
/* eslint-disable no-undef */
// my esling do not allow to pass JSX, maybe someone know how to resolve it without disabling eslint check
interface NotebookProps {
  title: string;
  body: JSX.Element;
  caption: JSX.Element;
  image: {
    defaultSrc: string;
    mobile: {
      path: string;
      width: number;
    };
    mobileRetina: {
      path: string;
      width: number;
    };
    desktop: {
      path: string;
      width: number;
    };
  };
}

import {
  Section,
  Wrapper,
  Title,
  Body,
  SlideshowInner,
  ImageContainer,
  Image,
} from "./elements";

const Notebook: React.SFC<NotebookProps> = ({
  title,
  body,
  caption,
  image,
}) => (
  <Section>
    <Row padding={30} gutterWidth={26} verticalAlignment="stretch">
      <Column span={1} spanMedium={0} />
      <Column span={10} spanMedium={12}>
        <Wrapper>
          <Title>{title}</Title>
          <Body>{body}</Body>
          <SlideshowInner>
            <ImageContainer>
              <Image
                src={image.defaultSrc}
                srcSet={`
                      ${image.mobile.path} ${image.mobile.width}w,
                      ${image.mobileRetina.path} ${image.mobileRetina.width}w,
                      ${image.desktop.path} ${image.desktop.width}w,
                    `}
                sizes={`
                      (max-width: 1680px) 100vw,
                      1680px,
                    `}
              />
            </ImageContainer>
          </SlideshowInner>
          {caption}
        </Wrapper>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Section>
);

export default Notebook;
